<template>
	<transition  name="move">
		<div class="food" v-show="showFlag" ref="food">
			<div class="food-content">
				<div class="image-header">
					<img :src="food.image">
					<div class="back" @click="hide">
						<i class="icon-arrow_lift"></i>
					</div>
				</div>
				<div class="content">
					<h1 class="title">{{food.name}}</h1>
					<div class="detail">
						<span class="sell-count">月售{{food.sellCount}}份</span>
					    <span class="rating">好评率{{food.rating}}%</span>
					</div>
					<div class="price">
						<span class="new">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
					</div>
					<div class="cartcontrol-wrapper">
				   	    <cartcontrol :food="food" @add-to-cart="addToCart"></cartcontrol>
				    </div>
					<div class="buy" 
					   	v-show="!food.count || food.count=== 0"
					   	@click.stop.prevent="addFirst($event)">
					   	加入购物车
					</div>
			  </div>
			    <split v-show="food.info"></split>
			    <div class="info" v-show="food.info">
			    	<h1 class="title">商品介绍</h1>
			    	<p class="text">{{food.info}}</p>
			    </div>
			    <split></split>
			    <div class="rating">
			    	<h1 class="title">商品评价</h1>
			    	<ratingselect
			    		:select-type="selectType"
			    		:only-content="onlyContent"
			    		:desc="desc"
			    		:ratings="food.ratings"
			    		@ratingtype-select="changeSelectType"
			    		@content-toggle="toggleOnlyContent">
			        </ratingselect>
			        <div class="rating-wrapper">
			        	<ul v-show="food.ratings && food.ratings.length">
			        		<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
			        			<div class="user">
			        				<span class="name">{{rating.username}}</span>
			        			    <img class="avatar" width="12" height="12" :src="rating.avatar">
			        			</div>
			        			<div class="time">{{rating.rateTime | formatDate}}</div>
			        			<p class="text">
			        				<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
			        				{{rating.text}}
			        			</p>
			        		</li>
			        	</ul>
			        	<div class="no-rating"
			        		v-show="!food.ratings || !food.ratings.length">暂无评价</div>
			        </div>
			    </div>
			</div>
	    </div>
	</transition>
</template>

<script>
import Vue from 'vue';
import BScroll from 'better-scroll';
import cartcontrol from'../cartcontrol/cartcontrol.vue';
import split from'../split/split.vue';
import ratingselect from '../ratingselect/ratingselect.vue';
import {formatDate} from '../../common/js/date.js';

const POSITIVE = 0;  // 推荐
const NEGATIVE = 1;  // 吐槽
const ALL = 2;  // 全部

export default{
	props:{
		food:{
			type:Object
		}
	},
	data(){
		return{
			showFlag:false,
			selectType:ALL,
			onlyContent:false,
			desc:{
				all:'全部',
				positive:'推荐',
				negative:'吐槽'
			}
		};
	},
	methods:{
		// 显示
		show(){
			// 初始化
			this.selectType = POSITIVE;
			this.onlyContent = false;
			this.showFlag = true;
			this.$nextTick(() => {
				if(!this.scroll){
					this.scroll=new BScroll(this.$refs.food,{
						click:true
					});
				}else{
					this.scroll.refresh();
				}
				
			});
		},
		// 隐藏
		hide(){
			this.showFlag=false;
		},
		// 加入购物车
		addFirst(event){
			if(!event._constructed){
			    return;
		    }
			this.$emit('add-to-cart',event.target);
			Vue.set(this.food,'count',1);
		},
		// 注册事件 实现动画小球
		addToCart(target){
			if(!event._constructed){
			    return;
		    }
			this.$emit('add-to-cart',target);
		},
		// 改变 selectType
		changeSelectType(type){
			this.selectType=type;
			this.$nextTick(() => {
				this.scroll.refresh();
			});
		},
		// 改变 onlyContent
		toggleOnlyContent(con){
			this.onlyContent=con;
			this.$nextTick(() => {
				this.scroll.refresh();
			});
		},
		// 判断 评论是否显示
		needShow(type,text){
			// 先判断是否存在内容和是否只显示有内容的评价
			if(this.onlyContent && !text){
				return false;
			}
			// 判断是否显示全部评价
			if(this.selectType === ALL){
				return true;
			}else{  // 显示选择类型对应的评价
				return type === this.selectType;
			}
		}
	},
	filters:{
		formatDate(time){
			let date=new Date(time);
			return formatDate(date,'yyyy-MM-dd hh:mm');
		}
	},
	// 注册组件
	components:{
		cartcontrol,
		split,
		ratingselect
	}
};
</script>

<style lang="scss" scoped>
@import "../../common/styles/mixin.scss";
.food{
	position:fixed;
	left:0;
	top:0;
	bottom:48px;
	z-index:30;
	width:100%;
	background:#fff;
	transition:all 0.2s linear;
	transform:translate3d(0,0,0);
	&.move-enter,&.move-leave-to{
		transform:translate3d(100%,0,0);
    }
    .image-header{
    	position:relative;
    	width:100%;
    	height:0;
    	padding-top:100%;
    	img{
    		position:absolute;
    		top:0;
    		left:0;
    		width:100%;
    		height:100%;
    	}
    	.back{
    		position:absolute;
    		top:10px;
    		left:0;
    		.icon-arrow_lift{
    			display:block;
    			padding:10px;
    			font-size: 20px;
    			color:#fff;
    		}
    	}
    }
    .content{
    	padding:18px;
    	position:relative;
    	.title{
    		margin-bottom:8px;
    		line-height: 14px;
    		font-size:14px;
    		font-weight:700;
    		color:rgb(7,17,27);
    	}
    	.detail{
    		margin-bottom:18px;
    		height:10px;
    		line-height:10px;
    		font-size:0;
    		.sell-count,.rating{
    			font-size:10px;
    			color:rgb(147,153,159);
    		}
    		.sell-count{
    			margin-right:12px;
    		}
    	}
    	.price {
			.new {
				margin-right: 8px;
				font-size: 14px;
				color: rgb(240, 20, 20);
				font-weight: 700;
			}
			.old {
				text-decoration: line-through;
				font-size: 10px;
				color: rgb(147, 153, 159);
				font-weight: 700;
			}
		}
		.cartcontrol-wrapper{
    	position:absolute;
    	right:12px;
    	bottom:12px;
	    }
	    .buy{
	    	position:absolute;
	    	right:18px;
	    	bottom:18px;
	    	z-index:10px;
	    	height:24px;
	    	line-height: 24px;
	    	padding:0 12px;
	    	box-sizing:border-box;
	    	font-size:10px;
	    	color:#fff;
	    	background:rgb(0,160,220);
	    	border-radius:12px;
	    }
    }
    .info{
    	padding:18px;
    	.title{
    		line-height: 14px;
    		margin-bottom:6px;
    		font-size:14px;
    		color:rgb(7,17,27);
    	}
    	.text{
    		line-height: 24px;
    		font-size: 12px;
    		font-weight: 200;
    		color:rgb(77,85,93);
    	}
    }
    .rating{
    	padding-top:18px;
    	.title{
    		line-height: 14px;
    		font-size:14px;
    		color:rgb(7,17,27);
    		margin-left:18px;
    	}
    	.rating-wrapper{
    		padding:0 18px;
    		.rating-item{
    			position:relative;
    			padding:16px 0;
    			@include border-b-1px(rgba(7, 17, 27, 0.1));
    			.user{
    				position:absolute;
    				right:0;
    				top:16px;
    				line-height:12px;
    				font-size:0;
    				.name{
    					display:inline-block;
    					margin-right:6px;
    					vertical-align:top;
    					font-size:10px;
    					color:rgb(147,153,159);
    				}
    				.avatar{
    					border-radius:50%;
    				}
    			}
    			.time{
    				margin-bottom:6px;
    				line-height:12px;
    				font-size:10px;
    				color:rgb(147,153,159);
    			}
    			.text{
    				line-height:16px;
    				font-size: 12px;
    				color:rgb(7,17,27);
    				.icon-thumb_up,.icon-thumb_down{
    					margin-right:4px;
    					line-height: 16px;
    					font-size:12px;
    				}
    				.icon-thumb_up{
    					color:rgb(0,160,220);
    				}
    				.icon-thumb_down{
    					color:rgb(147,153,159);
    				}
    			}
    		}
    		.no-rating{
    			padding:16px 0;
    			font-size:12px;
    			color:rgb(147,153,159);
    		}
    	}
    }
    
}

</style>


